import { BiImage} from 'react-icons/bi';
import { BsFileEarmarkPdfFill } from 'react-icons/bs';
import { ImMusic } from 'react-icons/im';


const filepath = [{
        id: 1,
        name: 'Image',
        img: 'images/image.png',
        link: 'image',
        icon:<BiImage />,
        content:"In steganography, LSB substitution method is used. I.e. since every image has three components (RGB). This pixel information is stored in encoded format in one byte. The first bits containing this information for every pixel can be modified to store the hidden text. For this, the preliminary condition is that the text to be stored has to be smaller or of equal size to the image used to hide the text."
        
    },
    {
        id: 2,
        name: 'Audio',
        img: 'images/audio.png',
        link: 'audio',
        icon:<ImMusic />,
        content:'Audio steganography is about hiding the secret message into the audio. It is a technique uses to secure the transmission of secret information or hide their existence. It also may provide confidentiality to secret message if the message is encrypted. To date most of the steganography software such as Mp3Stego and DeepSound use block cipher such as Advanced Encryption Standard or Data Encryption Standard to encrypt the secret message. It is a good practice for security'
    },
    {
        id: 3,
        name: 'Image in Image',
        img: 'images/imageinimage.png',
        link: 'imageinimage',
        icon:<BsFileEarmarkPdfFill />,
        content:'Embed the info into an image using traditional image steganography tools then embed the image into the Image Set the text color to white and make sure you are not using a colored background Superimpose a white rectangle over your text .Using the appropriate low level tools, create the text objects of your secret text then mark those objects as deleted. As long as no one tries to compact your PDF (Pitstop is one such compression/cleaning tool), your data will exist.'
    },
    {
        id: 4,
        name: 'Video',
        img: 'images/Video.png',
        link: 'video',
        icon:<BsFileEarmarkPdfFill />,
        content:'Embed the info into an image using traditional image steganography tools then embed the image into the Image Set the text color to white and make sure you are not using a colored background Superimpose a white rectangle over your text .Using the appropriate low level tools, create the text objects of your secret text then mark those objects as deleted. As long as no one tries to compact your PDF (Pitstop is one such compression/cleaning tool), your data will exist.'
    },
]

export default filepath

