const States = (state = {
    message: '',
    password: '',
    file: File,
    sumbit: false,
    response: '',
    button: ''
}, action) => {
    switch (action.type) {
        case 'MESSAGE':
            return {
                ...state, message: action.payload
            };
        case 'PASSWORD':

            return {
                ...state, password: action.payload
            };
        case 'FILE':
            return {
                ...state, file: action.payload
            };
        case 'FORM_RESET':
            return {
                ...state, message: action.payload, password: action.payload, file: action.payload, sumbit: false, response: action.payload, button: action.payload
            }
            case 'SUMBIT_FALSE':
                return {
                    ...state, sumbit: action.payload, response: ''
                }
                case 'BUTTON':
                    return {
                        ...state, button: action.payload
                    }
                    case 'SUMBIT_FORM_DECODE':
                        return {
                            ...state, response: action.payload, sumbit: true
                        };
                    case 'SUMBIT_FORM_ENCODE':
                        return {
                            ...state, response: action.payload, sumbit: true
                        };
                    default:
                        return state;
    }
}

export default States