import * as api from '../../api/index'

export const UpdateMessage = (message) => {
    return (dispatch) => {
        dispatch({
            type: 'MESSAGE',
            payload: message
        })
    }

}

export const UpdatePassword = (password) => {
    return (dispatch) => {
        dispatch({
            type: 'PASSWORD',
            payload: password
        })
    }
}

export const UpdateFile = (file) => {
    return (dispatch) => {
        dispatch({
            type: 'FILE',
            payload: file
        })
    }
}

export const SetSumbitTrue = () => {
    return (dispatch) => {
        dispatch({
            type: 'SUMBIT_TRUE',
            payload: true
        })
    }
}

export const SetSumbitFalse = () => {
    return (dispatch) => {
        dispatch({
            type: 'SUMBIT_FALSE',
            payload: false
        })
    }
}

export const ResetForm = () => {
    return (dispatch) => {
        dispatch({
            type: 'FORM_RESET',
            payload: ''
        })
    }
}

export const SetButton = () => {
    return (dispatch) => {
        dispatch({
            type: 'BUTTON',
            payload: 'is-loading'
        })
    }
}


export const SumbitFormEncode = (formdata, option) => async (dispatch) => {
    try {
        if (option === 'image') {
            const {
                data
            } = await api.PostImageEncode(formdata);
            dispatch({
                type: 'SUMBIT_FORM_ENCODE',
                payload: data
            });
            console.log(data)
        } else {
            const {
                data
            } = await api.PostAudioEncode(formdata);
            setTimeout(() => {
                dispatch({
                    type: 'SUMBIT_FORM_ENCODE',
                    payload: data
                });
            }, 5000);
            console.log(data);
        }

    } catch (error) {
        console.log(error)
    }

}

