import React from "react";

//Redux-Files
import { useSelector, useDispatch } from "react-redux";
import { UpdateFile, UpdatePassword, SumbitFormEncode, UpdateMessage, SetButton } from "../../redux/actions/actions";

//Page Components

import { BsUpload, BsDownload } from "react-icons/bs";

const Encode = ({ option, link }) => {

    const dispatch = useDispatch();
    const FormDatas = useSelector(state => state.States);

    const handleSumbit = (e) => {
        console.log(link);
        const isvalid = (link === 'image') ? FormDatas.file.name.includes('.png') : (link === 'audio') ? FormDatas.file.name.includes('.wav') : false;
        console.log(isvalid);
        e.preventDefault();
        if (isvalid) {
            e.preventDefault();
            let formdata = new FormData();
            formdata.append('message', FormDatas.message);
            formdata.append('password', FormDatas.password);
            formdata.append('file', FormDatas.file);
            dispatch(SumbitFormEncode(formdata, link));
        }
        else {
            console.log('error');
        }
    }
    return (
        <div>
            <div className='hero-body container'>
                <label className="label is-large">Upload to {option} {link}</label>
                <form onSubmit={(e) => handleSumbit(e)}>

                    <div className="field">
                        <div className="control has-icons-left has-icons-right">
                            <input className="input is-large" type='text' placeholder={`Secret Messsage`}
                                value={FormDatas.message || ''} onChange={(e) => dispatch(UpdateMessage(e.target.value))} />
                        </div>
                    </div>

                    <div className="field">
                        <div className="control has-icons-left has-icons-right">
                            <input className="input is-large" type='password' placeholder={`Secret Password`}
                                value={FormDatas.password || ''} onChange={(e) => dispatch(UpdatePassword(e.target.value))} />
                        </div>
                    </div>

                    <div className="file is-medium is-boxed has-name">
                        <label className="file-label">
                            <input className='file-input' type="file" name='file'
                                onChange={(e) => dispatch(UpdateFile(e.target.files[0]))} />
                            <span className="file-cta">
                                <span className="file-icon"><BsUpload /></span>
                                <span className="file-label">Upload your file</span>
                            </span>
                            <span className="file-name">{FormDatas.file.name || ''}</span>
                        </label>
                    </div>
                    <div className="level  column">
                        {FormDatas.sumbit ? <a href={FormDatas.response} download className={'button is-large is-primary'} >
                            <span className='icon'><BsDownload /></span>
                            <span>Download</span></a>
                            : <button className={`button is-large is-primary ${FormDatas.button}`} onClick={() => dispatch(SetButton('is-loading'))}>Sumbit</button>}</div>
                </form>
            </div>
        </div>

    )
}
export default Encode