import axios from "axios";

const imgencurl = 'http://localhost:3001/image/encode'
const imgdecurl = 'http://localhost:3001/image/decode'
const audioencurl = 'http://localhost:3001/audio/encode'
const audiodecurl = 'http://localhost:3001/audio/decode'

const config = {
    headers: {
        'content-type': 'multipart/form-data'
    }
};


export const PostImageEncode = (formdata) => axios.post(imgencurl, formdata, config);
export const PostImageDecode = (formdata) => axios.post(imgdecurl, formdata, config);
export const PostAudioEncode = (formdata) => axios.post(audioencurl, formdata, config);
export const PostAudioDecode = (formdata) => axios.post(audiodecurl, formdata, config);
