import React, { useState, useEffect } from "react";
import { BiError } from "react-icons/bi";
import { IconContext } from "react-icons/lib";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { useSelector } from "react-redux";

const ModalDivs = React.memo(() => {

  const [active, setactive] = useState('');
  const sumbit = useSelector(state => state.States.sumbit);
  const response = useSelector(state => state.States.response);


  useEffect(() => {
    setactive('is-active');
  }, [response, sumbit]);

  if (response === 'Invalid Password') {
    return (
      <div className={`modal ${active}`} >
        <div className="modal-background"></div>
        <div className="modal-content"><section className='section hero is-danger'>
          <div className='container has-text-centered'>
            <IconContext.Provider value={{ size: '6em', style: { verticalAlign: 'middle', horizontalAlign: 'left' } }}><BiError /></IconContext.Provider>
            <span className='subtitle is-1'>Invalid Password</span></div>
        </section>
        </div>
        <button className="modal-close is-large" onClick={() => setactive('')} aria-label="close"></button>
      </div>
    )
  }
  else if (sumbit) {
    return (
      <div className={`modal ${active}`} >
        <div className="modal-background"></div>
        <div className="modal-content"><section className='section hero is-primary'>
          <div className='container has-text-centered'>
            <IconContext.Provider
              value={{ size: '6em', style: { verticalAlign: 'middle', horizontalAlign: 'left' } }}><AiOutlineCheckCircle /></IconContext.Provider>
            <span className='subtitle is-1'>They Message is : {response}</span></div>
        </section>
        </div>
        <button
          className="modal-close is-large"
          onClick={() => setactive('')} aria-label="close"></button>
      </div>
    )
  }
  else {
    return (<div></div>)
  }


})

export default ModalDivs