import {React,useState,memo} from 'react'
import {AiOutlineLock,AiOutlineUnlock,AiOutlineReload} from 'react-icons/ai';
import { IconContext } from 'react-icons/lib';
import { useDispatch } from 'react-redux';
import { ResetForm, SetSumbitFalse } from '../redux/actions/actions';
import Encode from '../components/AppComponents/Encode';
import Decode from '../components/AppComponents/Decode';

const ImagePage=({filepath})=> {
  const dispatch = useDispatch();
  const {link,name,content} = filepath; 
 const [encoding,setencode] = useState(0); 
 
 if(encoding===0)  {
      return(
        <div className='hero-body container ' >
          <section className="section is-medium">
          <h1 className="title">{name} Steagnography </h1>
          <br />
          <h1 className="subtitle">{content}</h1>
          </section>
          <div className='level'>
            <button className='button is-large'
            onClick={()=>{setencode(true)}}>
              <IconContext.Provider value={{className:'icon is-large'}} ><AiOutlineLock /></IconContext.Provider>
              <span>Encoding</span>
              </button>

        <div className='level-right'>
          <button className='button is-large' 
          onClick={()=>{setencode(false)}}>
            <IconContext.Provider value={{className:'icon is-large'}}><AiOutlineUnlock /></IconContext.Provider>
            <span>Decoding</span>
            </button>
            </div>
      </div>
      </div>

    )}
    else if (encoding!==true){
      return(
      <div className='hero-body container'>
       
        <h1 className='title level-left'> <div className='level-item'>
          <button className='button is-large is-rounded' onClick={()=>{setencode(!encoding);dispatch(SetSumbitFalse);}}>
          <IconContext.Provider value={{className:'icon'}}><AiOutlineUnlock /></IconContext.Provider>
          </button></div>
        <div className='level-item'>
          <button className='button is-medium is-rounded' onClick={()=>{dispatch(ResetForm());}}>
          <IconContext.Provider value={{className:'icon'}}><AiOutlineReload /></IconContext.Provider>
          </button>
          </div>
          <div className='level-item'>{name} Steagnography  </div></h1>
     
      {<Decode option={'decode'} link={link} />}
    </div>
      )}
    else if(encoding===true){
     return (
      <div className='hero-body container'>
          <h1 className='title level-left'> <div className='level-item'><button className='button is-large is-rounded' onClick={()=>{setencode(!encoding);dispatch(SetSumbitFalse());}}>
           <IconContext.Provider value={{className:'icon'}}><AiOutlineLock /></IconContext.Provider> </button></div>
           <div className='level-item'>
          <button className='button is-medium is-rounded' onClick={()=>{dispatch(ResetForm());}}>
          <IconContext.Provider value={{className:'icon'}}><AiOutlineReload /></IconContext.Provider>
          </button>
          </div>
           <div className='level-item'>{name} Steagnography  </div></h1>

      {<Encode option={'encode'} link={link} />}
      </div>
      )
    }
}

export default memo(ImagePage);
