
import {BrowserRouter as Router , Routes, Route } from 'react-router-dom';
import 'bulma';
import './css/App.css';
import HomePage from './pages/HomePage';
import Page from './pages/Page';
import Navbar from './components/AppComponents/Navbar';
import Footer from './components/AppComponents/Footer';
import filepath from './data';

function App() {
  return (
  <Router>
    <div>
      <Navbar />
      <Routes>
        <Route path="/" exact element={<HomePage />}  />
        <Route path='/image'  element={<Page filepath={filepath[0]} />} />
        <Route path ='/audio' element= {<Page filepath={filepath[1]}/>} />
        <Route path='/imageinimage' element={<Page filepath={filepath[2]} />} />
        <Route path='/video' element={<Page filepath={filepath[3]} />} />
       </Routes>
    </div>
    <Footer />
  </Router>    
  );
}

export default App;
