import React from 'react'
import { Link } from 'react-router-dom';
import filepath from '../../data';

function Menu() {
  return (
    <div className="container-fluid">
      <div className="row">

        {
          filepath.map(file => {
            const { name, img, content,link} = file;
            return (
              <div className="col-xl-3">
                <div className="card col-md-4 col-xl-12"> <img className="rounded img-fluid" src={img} alt="Card cap" />
                  <div className="card-body">
                    <h5 className="card-title">{name.toUpperCase()}</h5>
                    <p className="card-text">{content}</p>
                    <Link exact to={'/'+link}><button type="button" className="btn btn-primary">Click here</button></Link>
                  </div>
               
                </div>   <div class="col-md-4 col-xl-12">&nbsp;</div>
              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Menu