import React from 'react'
import { Link } from 'react-router-dom'

function Banner() {
  return (
    <div className="jumbotron">
	  <h1 className="display-4">StegImage</h1>
	  <p className="lead">A simple web based steagnography application</p>
	  <hr className="my-4" />
	  <p>Made using python </p>
	  <p className="lead"> <Link className="btn btn-primary btn-lg" exact to="/about" role="button">Learn more</Link> </p>
    </div>
  )
}

export default Banner