import React from 'react';
import Main from '../components/AppComponents/Main';
import Banner from '../components/AppComponents/Banner';


function HomePage() {
  const banner={
    title:'StegImage',
    sub:'A Simple Image Steagnography Web Application .',
    info:'Made using React JS, Node js and Python'
  }
  return (
    <div>
    <Banner banner={banner}/>
    <Main />
    </div>
  )
}

export default HomePage
